.auth {
    display: flex;
    flex-direction: column;
    
    width: 340px;
    margin: 0 auto;
    padding: 50px 30px 30px;

    border-radius: $border-radius-big;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);

    &__form {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__title {
        $font-size: 20px;

        margin: 0 auto;
        margin-bottom: 40px;

        font-weight: 500;
        font-size: $font-size;
        line-height: $font-size;
    }

    &__fields {
        display: flex;
        flex-direction: column;
    }

    &__buttons {
        display: flex;
        flex-direction: column;
        align-items: center;

        margin-top: auto;
    }

    &__button {
        margin-bottom: 14px;
    }

    &__field {
        margin-bottom: 16px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}
