.chat {
    display: flex;

    width: 100%;
    height: 100vh;

    &__list {
        display: flex;
        flex-direction: column;
        flex-shrink: 0;

        width: 310px;
        height: 100%;
        padding: 20px 10px;

        border-right: 1px solid #EAEAEA;

        &-header {
            display: flex;
            flex-direction: column;

            margin-bottom: 15px;
        }

        &-wrapper {
            max-height: calc(100vh - 93px);

            overflow-y: auto;
        }
    }

    &__profile-link {
        position: relative;

        margin-left: auto;
        margin-bottom: 25px;
        padding-right: 16px;

        color: $label-color;

        &::after {
            position: absolute;
            top: 3px;
            right: 0;

            transform: rotate(45deg);

            width: 5px;
            height: 1px;

            background: $label-color;
            
            content: '';
        }

        &::before {
            position: absolute;
            top: 6px;
            right: 0;

            transform: rotate(-45deg);

            width: 5px;
            height: 1px;

            background: $label-color;
            
            content: '';
        }
    }

    &-field {
        display: flex;

        width: 100%;
        height: 100%;
        padding: 10px 20px 0;

        &__container {
            width: 100%;
        }

        &__header {
            display: flex;
            align-items: center;

            width: 100%;
            height: 54px;

            border-bottom: 1px solid $border-color;
        }

        &__avatar {
            $size: 34px;
    
            width: $size;
            height: $size;
            margin-right: 10px;
    
            border-radius: $border-radius-circle;
        }
    
        &__name {
            $size: 13px;
            
            font-size: $size;
            line-height: $size;
        }
    
        &__settings {
            display: flex;
    
            margin-right: 10px;
            margin-left: auto;
    
            background-color: transparent;
            border: 0;
    
            cursor: pointer;
        }

        &__content {
            padding-bottom: 30px;

            overflow-y: auto;
        }

        &__footer {
            display: flex;
            align-items: center;

            padding: 11px 0;

            border-top: 1px solid $border-color;
        }

        &__attach-button {
            $size: 32px;

            width: $size;
            height: $size;
            margin-right: 10px;
            padding: 0;

            background-color: transparent;
            border: 0;

            cursor: pointer;
        }

        &__message-input {
            width: 100%;
            margin-right: 10px;
            padding: 10px 20px;

            background-color: $search-color;
            border: 0;
            border-radius: $border-radius-huge;

            &::placeholder {
                color: $label-color;
            }
        }

        &__send-button {
            $size: 28px;

            width: $size;
            height: $size;
            padding: 0;

            background-color: transparent;
            border: 0;

            cursor: pointer;
        }
    }
}

.message-group {
    height: calc(100vh - 153px);
    &__wrapper {
        display: flex;
        flex-direction: column;

        padding-bottom: 30px;
    }

    &__date {
        $size: 12px;

        font-size: $size;
        line-height: $size;
        
        margin: 48px auto 30px;

        color: $label-color;
    }

    &__message {
        font-size: 12px;
        line-height: 15px;

        max-width: 402px;
        margin-bottom: 11px;
        padding: 11px;

        background-color: $background-color;

        border-radius: $border-radius-big;
        border-top-left-radius: 0;

        &_self {
            margin-left: auto;

            background-color: $primary-light-color;

            border-top-left-radius: $border-radius-big;
            border-bottom-right-radius: 0;
        }

        &-date {
            $size: 9px;

            font-size: $size;
            line-height: $size;

            margin-left: 28px;

            color: $primary-color;
        }

        &-image {
            width: 316px;
            height: 211px;

            object-fit: cover;

            border-radius: $border-radius-big;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}